:root {
  --black: #000000;
  --white: #FFFFFF;
  --dark-gray: #222222;
  --light-gray: #A0A0A0;
}

html {
  scroll-behavior: smooth;
  background-color: black;
}

.App {
  text-align: center;
  font-family: 'Gothic A1', sans-serif;
  font-weight: 100;
  overflow: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.row {
  display: inline-flex;
  flex-direction: row;
  width: 100%;
}

.blue-link {
  text-decoration: none;
  color: #3c80a6;
  transition-duration: 0.5s;
}

.blue-link:hover {
  color: #7dbfe6;
}

.column {
  display: inline-flex;
  flex-direction: column;
}

.section {
  background-color: black;
  min-height: 100vh;
  min-width: 100vw;
}

.container {
  background-color: black;
  display: flex;
  flex-direction: column;
  color: white;
  align-items: center;
  justify-content: center;
}

/* SM Bar */
.social-media-bar {
  position: fixed;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 2;
}

.social-media-bar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.social-media-bar ul li {
  margin: 20px 0;
}

.social-media-bar ul li a {
  color: #A0A0A0;
  font-size: 1.2em;
  text-decoration: none;
  transition: transform 0.3s ease, color 0.3s ease;
}

.social-media-bar ul li a:hover {
  color: #3c80a6;
}

.social-media-bar .line {
  width: 1px;
  background-color: #A0A0A0;
  margin-top: 20px;
}

.bar-text {
  transform: rotate(90deg);
  font-size: 0.8em;
  font-weight: 300;
}
