.Header {
    position: fixed;
    height: 7em;
    min-width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition-duration: 0.1s;
    z-index: 1;
}

.Logo {
    height: 4em;
    position: fixed;
    display: flex;
    margin-left: 3em;
    padding: auto;
    opacity: 0.7;
    transition-duration: 0.75s;
}

.Logo:hover {
    opacity: 1.0;
    cursor: pointer;
}

.Logo-link {
    position: fixed;
    display: flex;
    height: 60px;
}

.Sections {
    color: #A0A0A0;
    font-size: 0.9em;
    margin-left: auto;
    margin-right: 3em;
}

.Section-link {
    padding: 0.8em;
    font-weight: 300;
    transition-duration: 0.75s;
    color: #A0A0A0;
    text-decoration: none 
}

.Section-link:hover {
    color: white;
    cursor: pointer;
}
