/* HOME */
.Dynamic-text {
    color: #3c80a6;
    font-weight: 700;
}

.Typewriter-delay {
    animation: fadein 3.5s cubic-bezier(0.645,0.045,0.355,1) forwards;
    font-size: 0.7em;
}

@keyframes fadein {
    0% { opacity: 0 }
    100% { opacity: 1 }
}

.Intro-text {
    position: absolute;
    font-size: 3em;
}

img {
    user-drag: none;
    -webkit-user-drag: none;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}

.Bgrd-image {
    min-height: 100%;
    min-width: 100%;
    z-index: 0;
    opacity: 0.3;
}

.App-home {
    height: calc(100vh);
    z-index: -1;
}

.Home-container {
    display: inline-flex;
    flex-direction: column;
}

/* SECTIONS */

.Section-content {
    min-width: 70vw;
    min-height: 100%;
    background-color: black;
    align-items: center;
    justify-content: center;
    display: flex;
}

@media (max-width: 900px) {
    .Section-content {
        display: inline-flex;
        flex-direction: column;
    }

    .About-container {
        width: 60vw !important;
    }
}

@media (max-width:768px) {
    .Intro-text {
        font-size: 1.8em;
        margin-right: 60px;
        margin-left: 60px;
    }
    
    .Experience-nav {
        flex-direction: row;
        display: inline-flex;
        overflow: scroll;
    }

    .Experience-container {
        flex-direction: column !important;
    }

    .Experience-table {
        width: calc(100% - 40px) !important;
    }
}

@media (max-width: 600px) {
    .About-container {
        width: 90vw !important;
    }
    
    .Experience-container {
        width: 90vw !important;
    }

    .Section-content {
        display: inline-flex;
        flex-direction: column;
    }
}

/* ABOUT */
.About-container {
    display: inline-flex;
    width: 30vw;
    justify-content: center;
    align-items: center;
    max-height: 100%;
    flex-direction: column;
}

.About-text {
    width: 100%;
    height: 100%;
    font-size: 1em;
    font-weight: 300;
    text-align: left;
    color: #A0A0A0;
    line-height: 1.4;
}

.Header-text {
    text-align: left;
    width: 100%;
    height: 100%;
    display: flex;
    font-size: 1.6em;
    font-weight: 600;
    align-items: center;
    margin-bottom: 30px;
}

.Header-bar {
    border-bottom: 0.5px solid white;
    width: 100%;
    margin-left: 30px;
    opacity: 0.6;
    flex: 1;
}

.Portrait {
    height: 250px;
    width: 250px;
    border-radius: 50px;
    display: inline-flex;
    margin-top: 40px;
    margin-bottom: 40px;
}

/* EXPERIENCE */
.Experience-container {
    display: inline-flex;
    justify-content: center;
    width: 60vw;
    flex-direction: row;
    font-family: "Open Sans", sans-serif;
}

.Experience-table {
    background-color: rgb(160, 160, 160, 0.4);
    /* #A0A0A0 */
    width: calc(100% - 190px);
    font-size: 1em;
    text-align: left;
    padding: 20px;
    overflow: show;
}

.Experience-item {
    transition-duration: 0.25s;
    font-size: 0.8em;
    min-width: 70px;
    width: 150px;
    padding: 15px;
    text-align: left;
    font-weight: 300;
    color: #A0A0A0;
}

.Experience-item:hover {
    background-color: rgb(160, 160, 160, 0.4);
    cursor: pointer;
}

.Experience-title {
    margin: 0;
    opacity: 0.8;
    font-weight: 700;
    font-size: 1.2em;
    color: #E0E0E0;
}

.Experience-date {
    margin-top: 5px;
    margin-bottom: 10px;
    opacity: 0.8;
    font-weight: 400;
    font-size: 0.8em;
    color: #A0A0A0;
}

.Experience-bullets {
    list-style-type: none;
    margin: 0;
    padding: 0;
    font-weight: 400;
    font-size: 0.9em;
    line-height: 1.6em;
    color: #A0A0A0;
}

.Experience-bullets li {
    margin-left: 0;
    padding-left: 25px;
    position: relative;
}

.Experience-bullets li::before {
    content: '\25C7';
    position: absolute;
    left: 0;
    padding-right: 10px;
    font-size: 1em;
    vertical-align: middle;
    color: #3c80a6;
}